body {
  background: #afe1f7;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Helvetica Neue", "Ubuntu", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
h1 {
  font-size: clamp(3rem, 5vw, 7vw);
  color: #eee;
  font-weight: 700;
  margin: 0;
  padding: 0;
  user-select: none; /* supported by Chrome and Opera */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  align-items: center;
}
section {
  background: #6fc5a8;
  display: block;
  position: relative;
  width: 100%;
  height: auto;
  border-radius: 1rem;
  padding: 1rem;
  margin: auto;
  text-align: center;
}
.description {
  flex-direction: column;
  justify-content: center;
}
p {
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #242d60;
  height: 100%;
  width: 100%;
  padding: 20px;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}
.logo {
  border-radius: 6px;
  margin: 5px;
  width: 120px;
  height: 120px;
}
.image {
  text-align: center;
  justify-content: center;
  align-items: center;
  margin: auto;
  width: 100%;
}
h4,
h5 {
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #242d60;
  margin: 0;
}
h6 {
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #fff;
  margin: 0;
}
h5 {
  opacity: 0.5;
}
input,
label {
  margin-top: 0;
}
.button {
  background: #df62a4;
  border-radius: 0.4rem;
  width: 10rem;
  font-size: 1.5rem;
  border: 0rem;
  margin-top: 2%;
  text-align: center;
  color: #fff;
}
button:hover {
  opacity: 10.5;
  cursor: pointer;
  box-shadow: -1px 1px 8px #afe1f7;
  color: #afe1f7;
  width: 10.1rem;
  text-align: center;
  font-size: 1.6rem;
}
.planta {
  max-width: 300px;
}
